<template>
  <loading v-if="!content" />
  <div v-else-if="contentPresent" class="section">
    <section v-if="content.title || content.subtitle" class="container section-spacing">
      <div class="columns is-centered">
        <div class="column" :class="`is-${titleColumns}`">
          <Markdown class="title is-1" :input="pageTitle" tag="h1" />
          <Markdown class="subtitle is-4" :input="pageSubTitle" tag="h2" />
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column" :class="`is-${bodyColumns}`">
          <slot name="beforeContent" />

          <Markdown :input="pageContent" />

          <slot name="afterContent" />
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Loading from "@/components/Loading";
import Markdown from "@/components/Markdown";
import ContentService from "@/services/ContentService";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";
import { mapGetters } from "vuex";

export default {
  name: "PageContent",
  components: { Markdown, Loading },
  mixins: [markdownSubstitutionsMixin],
  props: {
    contentId: {
      type: String,
      required: true
    },
    titleColumns: {
      type: Number,
      default: 8
    },
    bodyColumns: {
      type: Number,
      default: 6
    }
  },
  data() {
    return {
      content: null
    };
  },
  created() {
    ContentService.getContent(this.contentId).then(res => {
      this.content = res.data.content;
    });
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person"
    }),
    contentPresent() {
      let present = false;
      if (this.content) {
        present =
          !!this.content.title ||
          !!this.content.subtitle ||
          !!this.content.body ||
          Object.keys(this.$slots).length > 0;
      }
      return present;
    },
    pageTitle() {
      return this.substituteContent(
        this.content.title,
        this.person,
        this.event
      );
    },
    pageSubTitle() {
      return this.substituteContent(
        this.content.subtitle,
        this.person,
        this.event
      );
    },
    pageContent() {
      return this.substituteContent(this.content.body, this.person, this.event);
    }
  }
};
</script>
